.hero {
  position: relative;
  max-width: 480px;
  margin-left: auto;
  margin-right: auto;
  @media screen and (min-width: $min-mobile + 1) {
    max-width: 768px;
  }
}
.slider {
  height: 580px;
}
.slider__image {
  display: none;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  &--mobile{
    display: block;
  }
}
.hero__content {
  position: absolute;
  width: 100%;
  max-width: 480px;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0px);
}
.hero__content-thumb {
  position: absolute;
  width: 100%;
  height: 580px;
  top: 0;
  background-color: rgba(25, 28, 38, 0.2);
}
.hero__slogan {
  @extend %slogan;
  color: $secondary-text-color;
  opacity: 60%;
  &::before {
    background-color: $secondary-text-color;
  }
}
.hero__title {
  @extend %title;
  color: $light-heading-color;
  margin-bottom: 20px;
  font-size: 48px;
  line-height: 1.17;
}
.hero__text {
  @extend %text;
  color: $secondary-text-color;
  max-width: 310px;
  font-size: 16px;
  line-height: 1.62;
}
