.header {
  position: absolute;
  z-index: 100;
  width: 100%;
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 40px;
}
.header__logo {
  color: $light-color;
}
.header__logo-icon {
  fill: currentColor;
}
.menu__button {
  padding: 0;
  background-color: transparent;
	border: none;
	z-index: 200;
}
.menu__icon {
  display: block;
}
.menu__icon-open {
  display: block;
  .menu__button.is-open & {
    display: none;
  }
}
.menu__icon-close {
  display: none;
  .menu__button.is-open & {
    display: block;
  }
}
.menu__mobile {
  opacity: 0;
  position: absolute;
  top: 0;
  right: 0;
  pointer-events: none;
	width: 100vw;
  height: 100vh;
  overflow: hidden;
  background-color: $transparent-bg-header-color;
  transition: opacity 250ms $cubic;
  &.is-open {
    opacity: 1;
    pointer-events: all;
    transition: opacity 300ms $cubic;
  }
}
.menu__content{
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  max-width: 337px;
  width: 100%;
  padding-top: 118px;
  padding-left: 40px;
  transform: translate3d(100%, 0, 0);
  background-color: $light-bg-color;
  transition: transform 250ms $cubic;
  .menu__mobile.is-open &{
    transform: translate3d(0, 0, 0);
    transition: transform 250ms $cubic 100ms;
    
  }
}
.nav {
	margin-bottom: 64px;
}
.menu__nav {
	@extend %list;
}
.menu__item {
	&:not(:last-child){
		margin-bottom: 24px;
	}
}
.menu__link, .header-tel__link{
	text-decoration: none;
	font-style: normal;
	font-weight: 600;
	font-size: 18px;
	line-height: 1.39;
	letter-spacing: 0.1em;
	color: $dark-link-color;
	transition: color 250ms $cubic;
	&:hover,
	&:focus {
		color: $accent-color;
	}
}
.header-tel {
	margin-bottom: 24px;
}
.header__button {
	margin-bottom: 52px;
}
.header__social {
	justify-content: start;
	flex-wrap: wrap;
}
.body{
	&.is-open{
		overflow: hidden;
	}
}